import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import {
  AppBar,
  Container,
  Dialog,
  IconButton,
  Toolbar,
  Typography,
  makeStyles,
} from '@material-ui/core'
import { Close } from '@material-ui/icons'

import ListErrors from '../ListErrors'
import ManagePassesTable from './ManagePassesTable'
import PageHeader from '../PageHeader'
import PageWrapper from '../PageWrapper'
import Spinner from '../Spinner'
import { SlideUpTransition } from '../../transitions'

import agent from '../../agent'

import {
  LOAD_TEACHER_PASSES,
  UNLOAD_MANAGE_PASSES,
} from '../../constants/actionTypes'

const useStyles = makeStyles((theme) => ({
  title: {
    color: theme.palette.common.white,
    margin: '0 auto',
  },
}))

const mapStateToProps = (state) => ({
  ...state.passes,
})

const mapDispatchToProps = (dispatch) => ({
  onLoad: () =>
    dispatch({ type: LOAD_TEACHER_PASSES, payload: agent.Passes.getAll() }),
  onUnload: () => dispatch({ type: UNLOAD_MANAGE_PASSES }),
})

const ManagePassesDialog = ({
  courses,
  errors,
  inProgress,
  isOpen,
  onClose,
  onLoad,
  onUnload,
}) => {
  const [open, setOpen] = useState(false)

  const classes = useStyles()

  const handleOpen = () => {
    setOpen(true)
    onLoad()
  }

  const handleClose = () => {
    setOpen(false)
    onUnload()
    onClose()
  }

  // Allow for external component control of displaying the dialog
  useEffect(() => {
    if (isOpen) handleOpen()
  }, [isOpen])

  return (
    <Dialog
      fullScreen
      open={open}
      onClick={(ev) => ev.stopPropagation()}
      onClose={handleClose}
      onFocus={(ev) => ev.stopPropagation()}
      TransitionComponent={SlideUpTransition}
    >
      <AppBar position='relative'>
        <Toolbar>
          <IconButton edge='start' onClick={handleClose}>
            <Close />
          </IconButton>
          <Typography variant='h5' className={classes.title}>
            Accounts
          </Typography>
        </Toolbar>
      </AppBar>
      <PageWrapper>
        <Container>
          <PageHeader title='Manage Hallpasses' />
          <ListErrors errors={errors} />
          {inProgress ? (
            <Spinner message='Loading...' />
          ) : (
            <ManagePassesTable courses={courses} />
          )}
        </Container>
      </PageWrapper>
    </Dialog>
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(ManagePassesDialog)
